// Muli

@font-face {
  font-family: 'Muli';
  src: url('../../assets/fonts/muli/muli-light-webfont.woff2') format('woff2'),
       url('../../assets/fonts/muli/muli-light-webfont.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Muli';
  src: url('../../assets/fonts/muli/muli-regular-webfont.woff2') format('woff2'),
       url('../../assets/fonts/muli/muli-regular-webfont.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Muli';
  src: url('../../assets/fonts/muli/muli-semibold-webfont.woff2') format('woff2'),
       url('../../assets/fonts/muli/muli-semibold-webfont.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Muli';
  src: url('../../assets/fonts/muli/muli-bold-webfont.woff2') format('woff2'),
       url('../../assets/fonts/muli/muli-bold-webfont.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

// Muli Italic 

@font-face {
  font-family: 'Muli Italic';
  src: url('../../assets/fonts/muli/muli-italic-webfont.woff2') format('woff2'),
       url('../../assets/fonts/muli/muli-italic-webfont.woff') format('woff');
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}