// form box

.form-box {
  @extend .box;
  padding: $spacing;
  // transition: transform 0.25s, box-shadow 0.25s;

  // &:focus-within {
  //   box-shadow: 0 0.2em 2em rgba(black,0.2);
  //   transform: scale(1.025); 
  // }

  button.button[type=submit] {
    @include bp-s-less {width: 100%;}
  }
}

// fieldset

fieldset > div:first-of-type {margin-top: 0.25rem;}

// form group structure

div.form-group {
  position: relative;
  margin: $vmargin 0;

  &.newsletter-request {
    text-align: left;
    span.text {font-size: $fs-18;}
  }

  span.label,
  p.label {
    display: block;
    margin-bottom: 0.25rem;
    font-weight: 400;
  }

  span.field {
    position: relative;
    display: block;
    margin: 0;

    &:before { // icons
      @extend .icon-extension;
      position: absolute;
      top: 0; right: 0.75rem;
      font-size: $fs-16;
      line-height: 2.8;
      z-index: 2;
    }

    input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
    textarea,
    select {
      width: 100%;
      
      @include bp-m {
        &.size-small {width: $ff-s;}
        &.size-medium {width: $ff-m;}
        &.size-large {width: $ff-l;}
      }
    } 

    // connected field & button

    &.field-button {
      display: flex;

      label {
        flex: 1 1 auto;

        input {border-right-color: transparent!important;}
      }

      button {flex: 0 0 auto;}
    }
  }

  &.linear-fields {
    @include bp-m {display: flex;}

    label {
      display: block;
      margin: $vmargin 0;

      &:first-of-type {margin-top: 0;}

      @include bp-m {
        flex: 1;
        margin: 0 0.25rem;
        &:first-of-type {margin-left: 0;}
        &:last-of-type {margin-right: 0;}
      }

      input {width: 100%;}
    }
  }
}

// form elements

input,
textarea,
select,
button {
  font-family: $ff-1;
  font-size: $fs-20;
  line-height: $lh-general;
}

input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
textarea,
.custom-select {
  border: 1px solid $clr-fields;
  border-radius: 0.25rem;
  padding: 0.6rem 0.75rem;
  background: white;
  color: $clr-1;
  box-sizing: border-box;
  box-shadow: none;
  outline: none;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;

  &:focus {
    box-shadow: 0 0 0 5px rgba($clr-fields,0.25);
    outline: none;
  }
}

textarea {
  display: block;
  width: 100%;
  //height: 9.25rem;
}

::-webkit-input-placeholder {color: $clr-placeholder;}
::-moz-placeholder {color: $clr-placeholder; opacity: 1;}
:-ms-input-placeholder {color: $clr-placeholder!important;}
::-ms-input-placeholder {color: $clr-placeholder!important;}
::placeholder {color: $clr-placeholder;}

// custom select box

.custom-select {
  padding: 0;

  &:before {
    @extend .icon-angle-down;
    color: $clr-3;
  }

  select {
    padding: 0.6rem 0.75rem;
    padding-right: 2.25rem;
    cursor: pointer;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
  }
}

// custom radio & checkbox buttons

label.custom-control {
  position: relative; // required for HTML error message position
  display: flex;
  align-items: flex-start;
  cursor: pointer;

  span {
    position: relative;
    flex: 0 0 auto;
    content: "";

    &.input {margin-right: 0.5rem;}

    &.text {
      display: block;
      flex: 0 1 auto;
    }
    
    // radio

    &.radio {
      display: block;
      border: 1px solid $clr-fields;
      border-radius: 50%;
      width: 25px;
      height: 25px;
      background: white;

      &:before {
        position: absolute;
        top: 4px; left: 4px;
        border-radius: 50%;
        width: 13px;
        height: 13px;
        background: $clr-b1;
      }
    }
    
    // checkbox

    &.checkbox {
      display: block;
      border: 1px solid $clr-fields;
      border-radius: 2px;
      width: 23px;
      height: 23px;
      background: white;

      &:before {
        @extend .icon-extension;
        position: absolute;
        top: 3px; left: 3px;
        font-size: $fs-16;
        color: $clr-b1;
        content: none;
      }
    }
  }

  input {  
    @include hide-screen();
    top: 29px; left: 12px; // required for HTML error message position

    &:checked {
      ~ span.radio:before {content: "";}
      ~ span.checkbox:before {@extend .icon-checkbox;}
    }

    &:focus {
      ~ span.radio,
      ~ span.checkbox {box-shadow: 0 0 0 5px rgba($clr-fields,0.25);}
    }
  }
}

// compact form

div.compact-form {
  margin-bottom: $vmargin;

  div.form-group {
    margin: 0.75rem 0 0;

    span.field {
      position: relative;
      z-index: 1;

      label {

        input {
          position: relative;
          z-index: 2;
          border-radius: 0.25rem 0 0 0.25rem!important;
          padding: 0.5rem 0.75rem!important;
          width: 100%!important;

          footer.footer & {font-size: $fs-18;}

          &:focus {
            box-shadow: none!important;

            + span.statement {
              right: 0; left: 0;
              opacity: 1;
              transition: left 0s, right 0s, opacity 0.25s;

              + span.active {display: block;}   
            }  
          }
        }

        span.statement {
          position: absolute;
          bottom: 100%; left: -100em; right: 100em;
          z-index: 3;
          margin-bottom: 0.5rem;
          border-radius: 0.5rem;
          padding: 0.5rem 1rem;
          background: $clr-1;
          font-size: $fs-14;
          text-align: center;
          color: white;
          opacity: 0;
          transition: left 0s 0.5s, right 0s 0.5s, opacity 0.25s 0.25s;
          
          @include bp-l {
            span {
              footer & {display: block;}
            }
          }

          a {color: white;}

          // this is the bubble point
          &:before { 
            position: absolute;
            top: 100%; left: 50%;
            margin-left: -8px;
            border: 8px solid transparent;
            border-top-color: $clr-1;
            content: "";
          }
        }

        // this is the pseudo active shadow
        span.active {
          display: none;
          position: absolute;
          top: 0; left: 0; bottom: 0; right: 0;
          z-index: 1;
          border-radius: 0.25rem;
          box-shadow: 0 0 0 5px rgba($clr-fields,0.25);
        }
      }

      button {
        position: relative;
        z-index: 2;
        border-radius: 0 0.25rem 0.25rem 0;
        padding: 0.5em 0.75em;
        box-shadow: inset 0 0 0 rgba(black,0.05), inset 0 1px 1px rgba(white,0.6);
        
        footer.footer & {
          background: $clr-2;
          font-size: $fs-18;
        }

        @include bp-m {
          &:hover,
          &:focus {box-shadow: inset 0 -4rem 0 rgba(black,0.05);}

          &:active {
            box-shadow: inset 0 4rem 0 rgba(black,0.1), inset 0 1px 2px rgba(black,0.75);
            transition: box-shadow 0.25s;
          }
        }
      }
    }
  }
}

// reveal fields

div.reveal-fields {
  text-align: left;

  input#reveal-fields {
    @include hide-screen();

    &:checked {
      + label.button-reveal {
        display: none;

        &:before {@extend .icon-minus-circle;}
        + div.form-group {display: block;}
      }
    }

    &:focus {
      + label.button-reveal {
        box-shadow: 0 0 0 5px rgba($clr-fields,0.25);
      }
    }
  }

  label.button-reveal {
    display: inline-block;
    border: 1px solid $clr-fields;
    border-radius: 0.25rem;
    padding: 0.25rem 0.5rem;
    background: rgba(black,0.05);
    text-align: center;
    font-size: $fs-16;
    color: #555;
    cursor: pointer;

    div.contact-form-inline & {
      border-color: #111;
      background: rgba(white,0.05);
      color: $clr-3;

      @include bp-m {
        &:hover {background: rgba(white,0.1);}
      }
    }

    @include bp-m {
      &:hover {background: rgba(black,0.1);}
    }
  }

  div.form-group {display: none;}
}
