// header

body.landing {
  &:before,
  &:after {content: none;}

  header.banner {
    position: absolute;
    top: $vmargin;

    div.wrap {
      padding: 0 $gutter;
      @include bp-m {padding: 0 ($gutter*2);}

      p.logo {
        position: static;
        margin: 0 auto;
        width: 250px;
        background: url(../../assets/images/logos/inverted-mantra-systems.svg) no-repeat 50% 100%; 
        background-size: 100% auto;

        @include bp-m {margin: 0;}
        @include bp-l {width: 300px;}

        &:before {
          display: block;
          padding-top: 13%;
          content: "";
        }

        span {@include hide-screen();}
      }
    }
  }
}

// landing intro

div.landing-intro {
  padding: ($spacing*4) 0 0;

  @include bp-m {
    padding: ($spacing*4) 0 ($spacing*2);
    div.row {align-items: center;}
  }

  div.text {
    text-align: center;
    @include bp-m {text-align: left;}

    h1 {
      @extend .h1-2;
      margin-bottom: $vmargin/2;

      @include bp-s-less {font-size: $fs-25;}

      span.keyword {
        display: block;
        line-height: 1rem;

        strong {
          display: inline-block;
          margin-bottom: 1rem;
          border-radius: 2rem;
          padding: 0.35rem 1rem;
          background: rgba(black,0.3);
          color: white;
          font-size: $fs-16;
          font-weight: 400;

          @include bp-m {font-size: $fs-18;}
        }
      }
    }

    p {
      @extend .lead;
      @include bp-m {margin-bottom: 0;}
    }
  }

  div.features {
    margin: 0 (-$gutter);
    padding: ($spacing*1.25) $spacing;
    background: rgba(black,0.3);
    text-align: center;

    @include bp-m {
      margin: $vmargin*2 0;
      border-radius: $border-radius;
      padding: $spacing*1.25;
    }

    @include bp-xl {padding: $spacing*2;}

    ul {
      display: inline-block;
      text-align: left;
      @include bp-m {font-size: $fs-22;}
      @include bp-xl {font-size: $fs-24;}
    }

    p {
      margin: 0;
      text-align: center;
      @include bp-l {font-size: $fs-24;}
    }
  }
}

// landing extra

div.landing-extra {
  padding-bottom: 0;
  text-align: center;

  @include bp-m {padding: ($spacing*2) 0;}
  @include bp-l {padding: ($spacing*3) 0;}

  div.wrap {max-width: 45rem;}
  h2 {@include bp-m {font-size: $fs-45;}}
  p.lead b {display: block;}

  p.cta {
    margin-bottom: 0;
    @include bp-l {font-size: $fs-24;}
  }
}

// landing content

div.landing-content {
  > h2:nth-of-type(1) {
    @include bp-m {
      font-size: $fs-45;
    }
  }

  > ul:not([class]):not([class]) {@extend .disc;}
  > ol:not([class]) {@extend .decimal;}

  figure.content-image {
    border-radius: $border-radius;
    padding: $spacing;
    background: $clr-4;
    text-align: center;

    @include bp-m {
      float: right;
      padding: $spacing*2;
      margin-left: $spacing;
    }

    img {display: inline-block;}
  }
}

// guide section

div.guide-section {
  background: $clr-4;

  div.guide-intro {padding-top: ($spacing*3.5);}
}