header.banner {
  position: absolute;
  top: 0; left: 0; right: 0;
  z-index: 1000;

  div.wrap {

    max-width: none;
    //@include bp-m {padding: 0 ($gutter*2);}

    a.content-jump {
      position: absolute;
      top: 0; left: -1000em;
      z-index: 10;
      padding: $nav-link-padding 0.4rem;
      background: $clr-b1;
      text-align: center;
      color: white;
      text-decoration: none;
      outline: none;

      &:active,
      &:focus {
        left: 0; right: 0;
      }
    }
  }

  nav {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    // logo

    p.logo {
      flex: 1 1 auto;
      position: relative;
      z-index: 3;
      margin: 0;
      font-size: $fs-18;

      @include bp-xl {font-size: $fs-20;}

      a {
        display: block;
        padding: 1.6rem 0 0.25rem;
        width: 200px;
        background: url(../../assets/images/logos/mantra-systems.svg) no-repeat 50% 88%; 
        background-size: 100% auto;
        text-decoration: none;

        body.nav-alt & {background-image: url(../../assets/images/logos/inverted-mantra-systems.svg)}

        @include bp-m {
          padding-top: $nav-link-padding;
          width: 235px;

          &:hover,
          &:focus,
          &.current {box-shadow: inset 0 5px $clr-b1;}
        }

        &:before {
          display: block;
          content: "\00A0";
        }

        > span {@include hide-screen();}
      }
    }

    // menu toggle

    p.menu-toggle {
      position: fixed;
      top: 0; right: 0;
      z-index: 10000;
      padding: 1rem 1rem 0 0;
      line-height: 1;
      cursor: pointer;

      label.button {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 2;
        border-radius: 3rem;
        padding: 0!important;
        width: 2.5rem;
        height: 2.5rem;
        background: white;
        font-size: $fs-25;
        color: $clr-1;

        @include bp-m {
          width: 3rem;
          height: 3rem;
          font-size: $fs-30;
              
          &:hover,
          &:focus {
            background: $clr-b1;
            color: white;
          }
        }

        &:before {
          @extend .icon-extension,.icon-bars;
        }

        span {@include hide-screen();}
      }
    }

    // contact options

    ul.contact-options {display: none;}

    // links

    div.links {
      position: absolute;
      left: -1000em;
      text-align: left;
      font-size: $fs-18;

      @include bp-l {
        position: static;
        display: flex;
        align-items: center;
        flex: 0 1 auto;
        padding-right: 2.25rem;
        width: auto;
      }

      @include bp-xl {font-size: $fs-20;}

      ul.site {
        margin: 0;

        @include bp-m {
          display: flex;
          align-items: center;
        }

        > li {

          @include bp-m {
            flex: 1 0 auto;
          }

          > a {
            display: block;
            padding: 0.5rem 0.75rem;
            font-weight: 500;
            color: white;
            text-decoration: none;

            @include bp-m {
              flex: 1 0 auto;
              border-radius: 0;
              padding: $nav-link-padding 0.65rem 0.25rem;
              background: none;
              color: $clr-1;

              body.nav-alt & {
                font-weight: 400;
                color: white;
              }

              @include bp-l {
                padding-left: 0.9rem;
                padding-right: 0.9rem;
              }

              &.current {
                color: $clr-b0;
                box-shadow: inset 0 5px $clr-b1;
                //body.nav-alt & {box-shadow: inset 0 5px white;}
              }

              &:hover,
              &:focus {
                color: $clr-b0;
                box-shadow: inset 0 5px $clr-b1;
              }
            }
          }

          &.guide {
            display: none;
            @include bp-l {display: block;}

            > a span {
              @include bp-m {position: absolute; left: -1000em;}
              @include bp-xl {position: static;}
            }
          }
        }
      }
          
      div.sub {display: none;}
    }
  }

  // menu toggle 

  input#menu-toggle {
    position: fixed;
    top: 50%; left: -1000em;
    z-index: -1;
    visibility: hidden;

    // -------------------------------------- menu open

    &:checked + nav {
      display: block;
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 9999;
      overflow-y: auto;
      background: $clr-1;
      -webkit-overflow-scrolling: touch;

      &:before {
        @extend .aesthetic;
        position: fixed;
        top: 0; left: 0; right: 0; bottom: 0;
        z-index: 1;
        background-repeat: no-repeat;
        background-position: 50%;
        background-size: cover;
        content: "";
        opacity: 0.2;

        @include bp-m {background-size: 200%;}
      }

      // logo

      p.logo {
        position: absolute;
        top: 1rem; left: 1rem;
        z-index: 3;
        margin: 0;
        padding: 0;

        a {
          @extend .button;
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          z-index: 2;
          border-radius: 3rem;
          padding: 0!important;
          width: 2.5rem;
          height: 2.5rem;
          background: white;
          font-size: $fs-25;
          color: $clr-1;

          @include bp-m {
            width: 3rem;
            height: 3rem;
            font-size: $fs-30;

            &:hover,
            &:focus {
              background: $clr-b1;
              color: white;
            }
          }

          &:before {
            @extend .icon-extension, .icon-home;
            padding: 0;
          }

          > span {@include hide-screen();}
        }
      }

      // menu toggle

      p.menu-toggle label.button {
        &:before {@extend .icon-times;}
      }

      // contact options

      ul.contact-options {
        position: relative;
        z-index: 2;
        display: flex;
        justify-content: center;
        margin: 0;
        padding: 1rem 0;
        font-size: $fs-25;

        li {
          display: inline-block;
          margin: 0 0.18rem;

          @include bp-m {margin: 0 0.5rem;}

          a.button {
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            z-index: 2;
            border-radius: 3rem;
            padding: 0!important;
            width: 2.5rem;
            height: 2.5rem;
            background: white;
            font-size: $fs-25;
            color: $clr-1;

            @include bp-m {
              width: 3rem;
              height: 3rem;
              font-size: $fs-30;

              &:hover,
              &:focus {
                background: $clr-b1;
                color: white;
              }
            }

            > span {@include hide-screen();}
          }
        }
      }

      // links

      div.links {
        overflow: hidden;
        position: relative;
        left: auto;
        z-index: 2;
        margin: 0 1rem 2rem;
        border-radius: $border-radius;
        font-size: $fs-17;

        @include bp-m {
          align-items: baseline;
          padding: 0;
        }

        @include bp-l {font-size: $fs-18;}

        ul.site {
          @include bp-m {
            align-items: stretch;
            flex: 1;
          }
          
          > li {
            margin-bottom: 4px;
            background: rgba(black,0.3);

            &.about {margin: 0;}
            &.contact,
            &.guide {display: none;}

            @include bp-m {
              flex: 1 1 auto;
              margin: 0 4px 0 0;
              width: 20%;
              background: rgba(black,0.2);

              &.consulting {width: 40%;}
              &.academy {width: 20%;}
              &.resources {width: 20%;}
            }

            &.consulting > a {background: $clr-b2;}
            &.academy > a {background: $clr-b3;}
            &.resources > a {background: $clr-b4;}
            &.about > a {background: $clr-b1;}

            > a {
              padding: 0.5rem 0.8rem;
              background: rgba(black,0.1);
              text-align: center;
              font-size: $fs-25;
              font-weight: 300;
              color: white;
              box-shadow: none;
              transition: background-color 0.1s;

              &.current {
                box-shadow: none;
              }

              @include bp-m {
                &:hover,
                &:focus {background: rgba(black,0.2);}
              }

              @include bp-l {padding: 0.6rem 1.25rem;}
            }
          }
        }

        div.sub {
          display: block;

          ul {
            margin: 0;

            li {

              a {
                display: block;
                padding: 0.6rem 0.8rem;
                text-decoration: none;
                color: white;
                box-shadow: inset 0 1px rgba(white,0.075);
                transition: background-color 0.1s;

                &.current {
                  background: rgba(black,0.4);
                  box-shadow: inset 5px 0 $clr-b1;
                }

                @include bp-m {
                  &:hover,
                  &:focus {background: rgba(black,0.2);}
                }

                @include bp-l {padding: 0.6rem 1.25rem;}
              }
            }
          }
        }
      }
    }
  }
}
