// strip

div.strip {
  padding: ($spacing*1.5) 0;
  background: white;
  @include bp-m {padding: ($spacing*2) 0;}

  &.section {
    padding: ($spacing*2) 0 ($spacing*1);
    @include bp-m {padding: ($spacing*3) 0 ($spacing*2);}

    h2 {@extend .h1-2;}

    &.team {
      h3 {
        margin-top: ($vmargin*2);
      }
    }
  }

  &.latest {
    p.lead span {display: block;}
  }

  &.guide p.lead a {color: white;}

  &.alt {padding-top: 0;}

  &.style-1,
  &.style-2,
  &.style-3,
  &.style-4,
  &.style-5 {
    position: relative;
    color: white;

    &:before {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      @extend .aesthetic;
      background-repeat: repeat;
      background-position: 50%;
      background-size: cover;
      content: "";
      opacity: 0.25;
    }

    &.faqs:before {
      background-size: 100% auto;
      background-position: 50% 0;
    }

    &:after {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      background-image: linear-gradient(to bottom, rgba(black,0.3) 0%, transparent 60%);
      content: "";
    }

    div.wrap {
      position: relative;
      z-index: 2;
    }
  }

  &.style-1 {background: $clr-b1;}
  &.style-2 {background: $clr-b2;} 
  &.style-3 {background: $clr-b3;} 
  &.style-4 {background: $clr-b4;} 
  &.style-5 {
    background: $clr-b5;
    color: $clr-1;

    &:after {content: none;}
  } 

  &.style-2,
  &.style-4 {
    &:before {transform: rotate(180deg);}
  }

  // call to actions

  &.cta {
    position: relative;
    z-index: 50;
    padding: ($spacing*1.5) 0;
    background-color: $clr-2;
    text-align: center;
    color: white;

    @include bp-m {text-align: left;}

    div.wrap {

      @include bp-m {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      @include bp-l {align-items: flex-end;}

      > h2 {
        @extend .lead;
        margin: 0;

        @include bp-m {
          flex: 0 1 auto;
          align-self: center;
        }

        span {
          font-size: $fs-20;
          
          span {
            @include bp-s {white-space: nowrap;}
          }
        }
      }

      > p {
        margin: $vmargin auto 0;

        @include bp-m {
          flex: 0 0 auto;
          align-self: center;
          margin: 0 0 0 $spacing;
        }

        a.button {
          @include bp-s-less {display: block;}
        }
      }
    }
    
    @include bp-l {
      &.spr-matcher,
      &.whitepaper-1,
      &.whitepaper-2,
      &.whitepaper-3 {

        span.image {
          flex: 0 0 auto;
          display: block;
          position: relative;
          width: 280px;
        
          &:before {
            position: absolute;
            left: -($spacing); bottom: -($spacing*1.5);
            padding-top: 69%;
            width: 100%;
            background: no-repeat 50% 100%;
            background-size: 100% auto;
            content: "";
          }
        }

        div.wrap h2 {flex: 1 1 auto;}
      }

      &.spr-matcher span.image:before {background-image: url(../../assets/images/content/cta-strips/spr-matcher.png);}
      &.whitepaper-1 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-1.png);}
      &.whitepaper-2 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-2.png);}
      &.whitepaper-3 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-3.png);}
    }

    &.contact {
      text-align: center;

      div.wrap {display: block;} /* required reset */

      div.col {

        h2 {@extend .lead;}

        > p {
          @include bp-m {margin: 0;}

          a.icon-phone {
            @extend .h2;
            text-decoration: none;
            color: white;

            &:before,
            span {
              display: inline-block;
              vertical-align: middle;
            }

            &:before {
              margin-right: 0.4rem;
              border-radius: 3rem;
              padding: 0.3rem;
              background: $clr-b1;
              font-size: $fs-16;

              @include bp-s {
                margin-right: 0.5rem;
                padding: 0.5rem;
                font-size: $fs-25;
              }
            }
          }
        }
      }
    }

    &.newsletter {
      div.compact-form {
        margin: 0;

        fieldset {
          > p {
            @extend .h3;
          }

          @include bp-m {
            display: flex;
            align-items: center;
          }

          div.form-group {
            margin: 0 auto;
            max-width: 520px;
            
            @include bp-m {
              flex: 1 1 auto;
              margin: 0 0 0 $spacing;
              min-width: 450px;
            }
          }

          > p {
            font-weight: 300;

            @include bp-m {
              flex: 0 1 auto;
              align-self: center;
              margin: 0;
            }
          }
        }
      }
    }
  }
}

// page intro

div.page-intro {
  padding: ($spacing*4) 0 ($spacing*2);
  text-align: center;

  @include bp-m {padding: ($spacing*5.5) 0 ($spacing*2.5);}

  body.about & {
    @include bp-m-less {padding-bottom: 0;}
  }

  h1 {
    margin-bottom: 0;

    body.error & {
      @extend .h2;
      span {
        display: block;
        font-size: $fs-70;
        @include bp-m {font-size: $fs-100;}
      }
    }
  }

  p.lead {
    margin: $vmargin auto 0;

    span {@include bp-s {display: block;}}
    @include bp-m {max-width: 960px;}
  }

  p.cta {margin: $vmargin 0 0;}
}

// consulting & events intro

div.consulting-intro,
div.events-intro {
  @extend .page-intro;

  &:before {background-position: 0 20%;}

  h1 {

    span.hierarchy {
      display: block;
      line-height: 1rem;

      strong {
        display: inline-block;
        margin-bottom: 1rem;
        border-radius: 2rem;
        padding: 0.35rem 1rem;
        background: rgba(black,0.3);
        color: white;
        font-size: $fs-16;
        font-weight: 400;

        @include bp-m {font-size: $fs-18;}
      }
    }

    span {
      @include bp-m {display: block;}
    }
  }
}

// guide intro

div.guide-intro {
  @extend .page-intro;

  @include bp-m {padding-bottom: $spacing*3;}

  &:before {background-position: 0 20%;}

  h1,h2 {
    body:not([class="guide index"]) & {@extend .h1-2;}
  }

  p.lead {margin-right: 0; margin-left: 0; max-width: none;}

  @include bp-s-less {p.cta label.button {display: block;}}
}

// home intro

div.home-intro {
  @extend .page-intro;
  overflow: hidden;
  position: relative;
  padding-bottom: $spacing*11;
  background: white;

  @include bp-m {padding-bottom: $spacing*14;}
  @include bp-l {padding-bottom: $spacing*17;}

  h1 {
    position: relative;
    z-index: 10;
    margin-bottom: $vmargin;

    @include bp-s-less {font-size: $fs-25;}
    @include bp-s {margin-bottom: $vmargin*1.5;}
    @include bp-m {margin-bottom: $vmargin*2;}
    @include bp-l {margin-bottom: $vmargin*2.5;}

    span {display: block;}
  }

  div.intro-funnels {

    ol {
      display: flex;
      margin: 0 (-$gutter);
      @include bp-m {margin: 0;}

      li {
        flex: 1 1 $col-4;

        div.content {

          &:before {
            position: absolute;
            top: 0; left: 0; right: 0; bottom: 0;
            z-index: 1;
            @extend .aesthetic;
            background-color: white;
            background-repeat: no-repeat;
            background-position: 50%;
            background-size: cover;
            box-shadow: inset 0 50px 50px 0 white; // hides the hard nav line during transition
            content: "";
            opacity: 0;
            transition: opacity 1s;
          }

          &:after {
            position: absolute;
            top: 0; left: 0; right: 0; bottom: 0;
            z-index: 2;
            content: "";
            opacity: 0;
            transition: opacity 1s;
          }

          &.intro-funnel-1 {
            &:after {
              background-image: linear-gradient(to bottom, white 0%, rgba($clr-b2,0.6) 60%);
            }

            @include bp-m {
              label {
                border-radius: $border-radius 0 0 $border-radius;

                &:hover,
                &:focus {background: $clr-b2;}
              }
            }
          }

          &.intro-funnel-2 {
            &:after {background-image: linear-gradient(to bottom, white 0%, rgba($clr-b3,0.6) 60%);}

            @include bp-m {
              label {
                &:hover,
                &:focus {background: $clr-b3;}
              }
            }
          }

          &.intro-funnel-3 {
            &:after {
              background-image: linear-gradient(to bottom, white 0%, rgba($clr-b4,0.6) 60%);
            }

            @include bp-m {
              label {
                border-radius: 0 $border-radius $border-radius 0;

                &:hover,
                &:focus {background: $clr-b4;}
              }
            }
          }

          label {
            overflow: hidden;
            position: relative;
            z-index: 10;
            display: block;
            padding: ($spacing/2) 0;
            background: rgba(white,0.7);
            font-size: $fs-18;
            font-weight: 300;
            cursor: pointer;
            user-select: none;
            -webkit-user-select: none;
            transition: background-color 0.7s, color 0.35s;

            @include bp-s {font-size: $fs-25;}

            @include bp-m {
              padding: $spacing 0;
              font-size: $fs-35;

              &:hover,
              &:focus {
                background: $clr-b1;
                color: white;
              }
            }

            @include bp-l {font-size: $fs-40;}
          }

          div.text {
            position: absolute;
            left: 0; right: 0;
            z-index: 10;
            margin-top: $vmargin;
            padding: 0 $spacing;
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.25s, visibility 0s 0.25s;

            @include bp-s {margin-top: $vmargin*1.5;}
            @include bp-m {margin-top: $vmargin*2;}
            @include bp-l {margin-top: $vmargin*2.5;}

            h2 {
              margin: 0 auto $vmargin;
              max-width: 970px;
              font-weight: 300;
              font-size: $fs-20;
              line-height: $lh-general;
              color: white;

              @include bp-s {font-size: $fs-25;}

              @include bp-m {
                margin-bottom: $vmargin*2;
                font-size: $fs-30;
              }

              @include bp-l {font-size: $fs-40;}
            }

            p {
              margin: 0 auto;
              
              a.button {
                display: block;
                @include bp-s {display: inline-block;}
                @include bp-m {font-size: $fs-30;}

                span:before {
                  @include bp-s-less {display: none;}
                }
              }
            }
          }
        }
      }
    }

    ol li input {
      @include hide-screen();

      &:checked + div.content {

        &:before {opacity: 1;}
        &:after {opacity: 1;}

        label {
          background: $clr-2;
          color: white;
          transition: background-color 1s, color 1s;

          + div.text {
            opacity: 1;
            visibility: visible;
            transition: opacity 0.75s 0.25s, visibility 0s 0.25s;
          }
        }
      }
    }
  }
}

// article pages

body.article {
  header.banner {
    position: relative;
    background: $clr-b4;

    div.wrap {
      position: relative;
      z-index: 2;
      padding-bottom: 1.4rem;
    }

    &:before {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      @extend .aesthetic;
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: cover;
      content: "";
      opacity: 0.2;

      @include bp-m {background-size: 100%;}
    }
  }

  article figure.headline + div.strip > div.wrap {
    h1 {@extend .h1-2;}
    > p:nth-of-type(1):not([class]) {@extend .lead;}
    > ul {@extend .disc;}
    > ol {@extend .decimal;}
  }
}

// guide pages

div.guide-content {

  @include bp-m {
    position: relative;
    z-index: 2;
    margin: -1.5rem 0 0;
    padding: 0 0 $vmargin;
    background: transparent;
  }

  div.wrap > div {

    @include bp-m {
      border-radius: 0.25rem;
      padding: ($spacing*2);
      background: white;
      box-shadow: 0 0 4px 2px rgba(black,0.1);
    }

    > ul:not([class]) {@extend .disc;}
    > ol:not([class]) {@extend .decimal;}
    > :last-child {margin-bottom: 0;}

    @include bp-m-less {
      p.back-top {@include hide-screen();}
    }
  }
}

// event pages

div.event-content {
  > p:nth-of-type(1):not([class]) {@extend .lead;}
  > ul:not([class]) {@extend .checked;}
  > ol:not([class]) {@extend .numbered-blocks;}
}

// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}

      &.col-4,
      &.col-6 {
        @include bp-m {&:nth-child(2) {margin-top: 0;}}
      }

      &.col-4 {
        @include bp-m {&:nth-child(3) {margin-top: 0;}}
      }
    }
  }
}

div.wrap > h2 + div.general-wrap,
div.wrap > h2 + p.lead + div.general-wrap {
  margin-top: $vmargin*1.5;
  @include bp-m {margin-top: $vmargin*2;}
}

// announcement

div.announcement {
  padding: ($spacing/1.5) 0;
  background: $clr-2;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {
    font-weight: 700;
    color: white;
  }
}

// promotions

div.promotion {
  padding: ($spacing/1.5) 0;
  background: $clr-b1;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {
    //font-weight: 700;
    color: white;
  }
}

// box 

div.box {
  overflow: hidden;
  margin: 0 0 $vmargin;
  border-radius: $border-radius;
  background: $clr-4;

  > div:first-of-type {margin-top: 0;}
  > :last-child {margin-bottom: 0;}

  div.col-4 & {font-size: $fs-18;}

  h2 {
    @extend .h3;
    margin: 0;
    background: $clr-2;

    a,
    span {
      display: block;
      padding: ($spacing/2) $spacing;
      color: white;
      text-decoration: none;
      transition: background-color 0.2s;
    }

    a {
      @include bp-m {
        &:hover,
        &:focus {background: rgba(black,0.2);}
      }
    }
  }

  div.content {
    padding: $spacing;
    > :last-child {margin-bottom: 0;}
  }

  // secondary nagivation

  nav {

    @include bp-m {padding-bottom: 0.5rem;}

    ul {
      margin: 0;

      li {
        a {
          display: block;
          padding: 0.5rem $spacing;
          text-decoration: none;
          color: $clr-1;
          transition: background-color 0.2s, color 0.1s;

          @include bp-m {
            &:hover,
            &:focus {
              background: rgba(white,0.6);
              color: $clr-b1;
            }
          }

          &.current {
            background: rgba(white,0.6);
            color: $clr-b1;
            box-shadow: inset 5px 0 $clr-b1;
          }
        }

        ul {
          font-size: $fs-17;
          li a:before {content: "\02013\000A0";}
        }
      }
    }
  }
}

