// notices

p.notice {
  position: relative;
  border: 3px solid $clr-3;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;

  //form & {margin: 0;}

  &:before {
    position: absolute;
    left: 0.8rem; top: 1.05rem;
    font-size: $fs-24;
  }

  &.error,
  &.success,
  &.warning,
  &.caution,
  &.offer {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    //form & {margin-top: $vmargin;}

    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.offer {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-bullhorn;
      color: $clr-success;
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }
}

// contact form inline

input#contact-form-inline {
  @include hide-screen();

  &:checked {
    + div.row {
      label.contact-form-inline-cta span[class*="icon-"]:before {@extend .icon-minus-circle;}
      + div.contact-form-inline {display: block;}
    }
  }
}

div.contact-form-inline {
  display: none;
  margin: $vmargin auto 0;
  border-radius: $border-radius;
  padding: $spacing;
  max-width: 30rem;
  background: $clr-1;

  form button.button {
    @include bp-s-less {width: 100%;}
  }
}

// service process

div.service-process {
  text-align: center;

  @include bp-m {
    div.row {
      align-items: center;

      div.text {
        text-align: left;
        ul.buttons {margin-bottom: 0;}
      }
    }
  }

  figure {
    @include bp-m {margin-bottom: 0;}

    img {display: inline-block;}
  }
}

img {
  &.process-1 {max-width: 229px;}
  &.process-2 {max-width: 279px;}
  &.process-3 {max-width: 247px;}
  &.process-4 {max-width: 259px;}
  &.process-5 {max-width: 315px;}
}

// mini guide funnels

div.guide-funnel {
  overflow: hidden;
  position: relative;
  margin: 0 (-$gutter) $vmargin;
  background: $clr-b1;
  text-align: center;

  &.style-2 {background: $clr-b2;}
  &.style-3 {background: $clr-b3;}
  &.style-4 {background: $clr-b4;}

  @include bp-s {
    margin: 0 0 $vmargin;
    border-radius: $border-radius;
  }

  @include bp-m {
    float: right;
    margin: 0 0 $vmargin $spacing;
    text-align: left;
    font-size: $fs-24;
  }

  &:before {
    position: absolute;
    top: 0; left: 0; right: 0; bottom: 0;
    z-index: 1;
    @extend .aesthetic;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
    content: "";
    opacity: 0.2;

    @include bp-m {background-size: 200%;}
  }

  a {
    display: block;
    position: relative;
    z-index: 2;
    padding: $spacing 0.5rem;
    color: white;
    text-decoration: none;
    transition: background 0.2s;

    @include bp-m {
      padding: $spacing;
      &:hover,
      &:focus {background: rgba(black,0.1);}
    }

    b {
      display: block;
      margin-top: 0.25rem;
      font-weight: 500;

      @include bp-m {
        display: flex;
        align-items: center;
        font-size: $fs-27;
      }

      &:before {
        display: block;
        font-size: $fs-35;

        @include bp-m {margin-right: 0.25em;}
      }
    }
  }
}

// guide service funnel

div.guide-service-funnel {
  position: relative;
  z-index: 1;
  overflow: hidden;
  margin: 0 (-$gutter);

  @include bp-m {
    margin: 0 0 $vmargin;
    border-radius: 0.25rem;
  }

  h2 {
    @extend .h3;
    margin: 0;
    padding: 1rem 1.5rem;
    background: $clr-2;
    color: white;
    //@include hide-screen();
  }

  div.content {
    position: relative;
    padding: $spacing $spacing 0;
    background: $clr-b2;
    color: white;

    @include bp-m {
      padding: $spacing;
      padding-left: ($spacing*2);
    }

    &:before {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      @extend .aesthetic;
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: cover;
      content: "";
      opacity: 0.3;
    }

    &:after {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      background-image: linear-gradient(to bottom, rgba(black,0.15) 0%, transparent 60%);
      content: "";
    }

    div.row.text {
      position: relative;
      z-index: 2;

      @include bp-m {align-items: center;}

      h3 {
        @extend .h2-3;
        margin-bottom: $vmargin/2;
        font-weight: 300;
      }

      p.lead {
        @include bp-m {margin-bottom: 0;}
      }

      div.features {
        margin: 0 (-$gutter);
        padding: ($spacing*1.5) $spacing;
        background: rgba(black,0.3);
        text-align: center;

        @include bp-m {
          margin: 0;
          border-radius: $border-radius;
          padding: $spacing*1.25;
        }

        ul {
          display: inline-block;
          text-align: left;
          @include bp-m {font-size: $fs-24;}
        }

        p {
          margin: 0;
          text-align: center;

          a.button > span > span > span {@include hide-screen();}
        }
      }
    }
  }
}

// supporting info - on guide and article pages

div.supporting-info {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 0 $vmargin;
  border-radius: 0.25rem;
  padding: 0.5rem 1rem;
  background: $clr-4;

  ul, p {margin: 0.25rem 0;}

  &.end {
    margin-bottom: 0;

    div.guide-nav-end + & {
      border-top: 5px solid $clr-3;
      background: none;
    }
  }

  ul.share-options {flex: 0 0 auto;}

  ul.general {
    flex: 1 0 auto;
    list-style: none;
    font-size: $fs-16;

    @include bp-m {display: flex;}

    li {
      display: flex;
      align-items: center;
      margin-right: 1rem;

      &:before {margin-right: 0.25rem;}

      a {
        text-decoration: none;
        font-weight: 600;
        color: $clr-1;

        @include bp-m {
          &:hover,
          &:focus {color: $clr-b1;}
        }
      }
    }
  }

  p.top {
    flex: 1 0 auto;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 2rem;
      width: 1.7rem;
      height: 1.7rem;
      background: $clr-b1;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      @include bp-m {
        &:hover,
        &:focus {
          box-shadow: inset 0 0 0 4rem rgba(black,0.1);
        }
      }

      span {@include hide-screen();}
    }
  }
}

// guide nav end

div.guide-nav-end {
  clear: both;
  border-radius: 0.25rem 0.25rem 0 0;
  padding: 0.75rem;
  background: $clr-4;
  text-align: center;

  @include bp-s-less {
    label.button {display: block;}
  }
}

// pdf embed

div.sample-preview {
  display: flex;
  align-items: stretch;
}

div.pdf-insert {
  flex: 1;
  position: relative;
  border-bottom: 5px solid #4F5356;
  background: #4F5356;
  box-shadow: 0 0 10px 5px rgba(black,0.1);

  @-moz-document url-prefix() {
    border-bottom: 20px solid #2A2A2E;
    background: #2A2A2E;
  }
  
  @include bp-m-less {display: none;}

  embed,
  object {
    position: absolute;
    top: 0; left: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background: rgba(white,0.3);
  }
}

p.pdf-legacy {
  padding: ($spacing*2);
  text-align: center;
}

// video embed

div.video-embed {
  position: relative;

  &.playing {
    &:before {
      display: block;
      padding-top: 56.23%;
      content: "";
    }
  }

  &.playing,
  a.cta {
    margin: 0 (-$gutter) $vmargin;
    background: black;

    @include bp-m {
      margin: 0 auto $vmargin;
      max-width: 600px;
      box-shadow: 0 0 10px 5px rgba(black,0.1);
    }

    div.page-intro &,
    div.landing-extra & {
      margin-top: ($vmargin*1.5);
      margin-bottom: 0;
      @include bp-m {margin-top: $vmargin*2;}
    }
  }

  a.cta {
    position: relative;
    display: block;
    transition: filter 0.2s;

    @include bp-m {
      &:hover,
      &:focus {filter: brightness(90%);}
    }

    &:before {
      position: relative;
      z-index: 2;
      display: block;
      padding-top: 56.23%;
      background: url(../../assets/images/content/other/play.png) no-repeat 50%;
      cursor: pointer;
      content: "";
    }

    img {
      position: absolute;
      right: 0; top: 0; left: 0; bottom: 0;
      z-index: 1;
      width: 100%;
      cursor: pointer;
    }
  }

  embed {
    position: absolute;
    top: 0; left: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background: transparent;
  }
}

// contact button

input.contact-button-toggle {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;
  visibility: hidden;

  &:checked + div.contact-button {
    z-index: 2000;

    label.cta {
      display: block;

      span.button {
        box-shadow: inset 0 1px 2px rgba(black,0.75), 0 0 0 5px rgba(white,0.1);
        &:before {@extend .icon-times;}
      }

      &:after {@include bp-s {content: "";}}
    }

    div.form {
      top: 0; right: 0;

      @include bp-s-less {left: 0;}

      @include bp-s {
        top: auto;
        margin-bottom: 5rem;
        opacity: 1;
        transition: margin-bottom 0.25s, opacity 0.25s;
      }
    }
  }
}

div.contact-button {
  position: fixed;
  bottom: 0; right: 0;
  z-index: 500;
  padding-bottom: 1rem;

  label.cta {
    display: none;
    cursor: pointer;

    @include bp-s {display: block;}

    span.button {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
      margin-right: 1rem;
      border-radius: 3rem;
      padding: 0!important;
      width: 3rem;
      height: 3rem;

      &:before {
        margin: 0!important;
        font-size: $fs-30;
      }

      span {@include hide-screen();}
    }

    &:after {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      background: rgba(black,0.3);
    }
  }

  div.form {
    position: fixed;
    bottom: 0; right: 1000em;
    z-index: 1;
    background: $clr-4;

    @include bp-s {
      margin: 0 1rem 4rem 0;
      border-radius: $border-radius;
      width: 300px;
      background: none;
      opacity: 0;
      box-shadow: 0.25rem 0.25rem 1.5rem rgba(black,0.3);
      transition: margin-bottom 0.25s, opacity 0.25s, right 0s 0.25s;
    }

    h2 {
      @extend .h3;
      position: relative;
      margin: 0;
      background: $clr-2;
      color: white;
      padding: 0.5rem 1rem;

      @include bp-s {border-radius: $border-radius $border-radius 0 0;}

      &:after {
        position: absolute;
        right: 1rem;
        top: 0;
        border-radius: 3rem;
        width: 3rem;
        height: 3rem;
        background: white url(../../assets/images/content/other/contact-button-person.jpg) no-repeat 50%;
        background-size: cover;
        box-shadow: 0 0 0 5px $clr-2;
        content: "";

        @include bp-s {top: -0.15rem;}
      }
    }

    form {
      padding: 1rem;
      background: $clr-4;

      @include bp-s {border-radius: 0 0 $border-radius $border-radius;}

      div.form-group {
        margin: 0 0 ($vmargin/2);

        &.linear-fields {
          margin-bottom: 0;

          label {
            margin-top: 0;
            margin-bottom: ($vmargin/2);
          }
        }

        input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
        textarea,
        .custom-select {
          padding: 0.4rem 0.5rem;
          font-size: $fs-18;
        }
      }

      button {
        width: 100%;
        font-size: $fs-20;
      }
    }
  }
}

// medical professional on about page

div.med-pro {
  position: relative;
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;
  background: $clr-4;
  text-align: left;

  @include bp-m {text-align: right;}

  &:before {
    display: block;
    height: 15rem;
    background: $clr-3 url(../../assets/images/content/other/medical-professionals.jpg) no-repeat 50%;
    background-size: cover;
    content: "";

    @include bp-m {
      position: absolute;
      top: 0; left: 0; bottom: 0;
      border-right: 4px solid white;
      width: 50%;
      height: auto;
    }
  }

  p {
    @extend .lead;
    margin-bottom: 0;
    padding: 1rem;

    @include bp-s {padding: 2rem;}
    
    @include bp-m {
      display: inline-block;
      padding: 4rem 2rem;
      width: 50%;
    }
  }
}

// mdr template

div.mdr-template {
  div.template-cta {order: 2;}
}

// buy details

div.buy-details {

  div.purchase-info {
    overflow: hidden;
    position: relative;
    margin: 0 (-$gutter) $vmargin;
    background: $clr-2;
    text-align: center;
    color: white;

    body.consulting & {background: $clr-b2;}
    body.academy & {background: $clr-b3;}
    body.resources & {background: $clr-b4;}

    @include bp-s {
      margin: 0 0 $vmargin;
      border-radius: $border-radius;
    }

    &:before {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      @extend .aesthetic;
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: cover;
      content: "";
      opacity: 0.2;

      @include bp-m {background-size: 200%;}
    }

    div.content {
      position: relative;
      z-index: 2;
      padding: $spacing;
    }

    p.prices {
      margin-bottom: 0.5rem;

      span {display: block;}
      b {@include hide-screen();}

      span.was {
        font-size: $fs-16;
        color: #777;

        span.price span {
          display: inline-block;
          text-decoration: line-through;
        }
      }

      span.current {
        span.price {
          font-size: $fs-30;
          line-height: 1.1;
        }

        span.vat {
          display: block;
          font-size: $fs-14;
        }

        span {
          @include bp-m {display: inline-block;}
        }
      }
    }

    ul.buttons {
      margin: 0;

      li {
        display: block;
        margin-right: 0;

        a.button {display: block;}
      }
    }

    p.cta {
      margin: 0;
      a.button {display: block;}
    }

    p.information {
      margin: 1rem 0 0;
      font-size: $fs-16;
    }
  }

  div.additional-info {
    border-radius: $border-radius;
    padding: $spacing;
    background: rgba($clr-4,0.4);
    font-size: $fs-16;

    p.list-lead {
      margin-bottom: 0.2rem;
      font-weight: 500;
    }

    ul.checked {
      margin-bottom: 0;
      
      li {margin: 0;}
    }

    ul.share-options {
      margin-top: $vmargin;
    }
  }
}

// template features

div.template-intro {

  ul.template-features {
    margin: 0;
    text-align: center;
    font-size: $fs-30;
    font-weight: 300;

    @include bp-m {font-size: $fs-40;}

    $fbw: 4px;

    li {
      padding: ($spacing) 0;
      border: 0 solid $clr-4;
      border-bottom-width: $fbw;

      @include bp-m {padding: ($spacing*2) $spacing;}

      &:last-child {border-bottom-width: 0;}

      @include bp-m {
        &:nth-child(1) {border-width: 0 $fbw $fbw 0;}
        &:nth-child(2) {border-width: 0 0 $fbw 0;}
        &:nth-child(3) {border-width: 0 $fbw 0 0;}
      }

      &:before {
        display: block;
        margin-bottom: 1rem;
        font-size: $fs-50;
        color: $clr-b4;
      }

      span {display: block;}
    }
  }
}

// service features

$fbw: 4px;

div.service-features {

  ul.row {
    margin: 0;
    text-align: center;

    li {
      display: flex;
      border: 0 solid $clr-4;
      border-top-width: $fbw;

      &:first-child {border-top-width: 0;}

      @include bp-m {
        border-right-width: $fbw;

        &:nth-child(-n+3) {border-top-width: 0;}
        &:nth-child(3n) {border-right-width: 0;}
      }

      a {
        flex: 1;
        position: relative;
        display: block;
        padding: ($spacing) 0;
        text-decoration: none;
        transition: background-color 0.15s;

        @include bp-m {
          padding: ($spacing*2) $spacing;

          &:before {
            opacity: 1;
            transition: opacity 0.15s;
          }

          &:after {
            position: absolute;
            top: 2.9rem; left: 50%;
            display: inline-block;
            margin: 0 0 0 -3.5rem;
            border-radius: 0.25rem;
            padding: 0.2rem 1rem;
            width: 7rem;
            background: white;
            text-align: center;
            font-size: $fs-18;
            color: $clr-b0;
            opacity: 0;
            content: "Learn more";
            transition: opacity 0.15s;
          }

          &:hover,
          &:focus {
            background: $clr-4;

            &:before {opacity: 0;}
            &:after {opacity: 1;}
          }
        }

        &:before {
          display: block;
          margin-bottom: 1rem;
          font-size: $fs-50;
          color: $clr-b2;
        }

        h3 {
          margin-bottom: 1rem;
          span {display: block;}
        }

        p {
          margin: 0;
          font-size: $fs-18;
          color: $clr-1;
        }
      }
    }
  }
}
