// spaced list

.spaced {
  li {margin: 0.5rem 0;}
}

// disc list

.disc {
  margin-left: 1.25rem;
  list-style: disc;

  ul {
    margin-left: 1.25rem;
    list-style: circle;
  }
}

// decimal list

.decimal {
  margin-left: 1.25rem;
  list-style: decimal;
}

// lower alpha list

.lower-alpha li {
  margin-left: 1.25rem;
  list-style: lower-alpha;

  ol {margin-bottom: 0;}
}

// defintion list

dt {
  @extend .h4;
  font-weight: 500;
}

// checked list

ul.checked {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.3em;

      body.consulting & {color: $clr-b2;}
      body.academy & {color: $clr-b3;}
      body.resources & {color: $clr-b4;}
    }
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// buttons

ul.buttons {

  div.service-process div.text & {margin-bottom: 0;}

  li {
    display: inline-block;
    margin-bottom: 0.5rem;
    margin-right: 0.2rem;

    @include bp-s-less {display: block;}

    .button {
      @include bp-s-less {display: block;}
    }
  }
}

// hierarchy links

ol.hierarchy {
  margin-bottom: $vmargin/2;
  font-size: $fs-16;

  li {
    display: inline-block;

    &:before {
      @extend .icon-extension, .icon-angle-right;
      margin-right: 0.25rem;
      font-size: $fs-10;
      vertical-align: middle;
    }
  
    a, span {
      text-decoration: none;
      vertical-align: middle;
    }
    
    a {
      @include bp-m {
        &:hover,
        &:focus {text-decoration: underline;}
      }
    }

    &:first-child:before {content: none;}
  }
}

// brand assets

ul.brand-assets {
  border-top: 1px solid $clr-3;

  li {
    border-bottom: 1px solid $clr-3;
    padding: 0.4rem 0;

    a {
      text-decoration: none;
      @include bp-m {
        &:hover,
        &:focus {text-decoration: underline;}
      }
    }
  }
}

// chevroned list

ul.chevroned {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-angle-right;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;

      body.consulting & {color: $clr-b2;}
      body.academy & {color: $clr-b3;}
      body.resources & {color: $clr-b4;}
    }
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// document list

ul.documents {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-file;
      position: absolute;
      left: 0; top: 1px;
      font-size: $fs-24;
    }
  }
}

// features

ul.features {
  margin-bottom: 0;
  border-radius: $border-radius;
  padding: $spacing;
  background: $clr-4;

  @include bp-s {padding: ($spacing*1.25);}

  li {
    position: relative;
    margin-top: 1rem;

    &:first-child {margin-top: 0;}

    @include bp-s {padding-left: 2.25rem;}

    &:before {
      display: block;
      margin-bottom: 0.25rem;
      font-size: $fs-40;
      color: $clr-b1;

      @include bp-s {
        position: absolute;
        top: -0.1rem; left: 0;
        margin-bottom: 0;
        font-size: $fs-30;
      }
    }

    h3 {
      @extend .h5;
      margin-bottom: 0.5rem;
    }

    p {
      margin-bottom: 0;
      font-size: $fs-18;
    }
  }
}

// numbered discs

ol.numbered-discs {
  counter-reset: numbered-discs;

  li {
    position: relative;
    margin-bottom: 0.5rem;
    padding-left: 2rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      margin-right: 1rem;
      border-radius: 50%;
      padding: 0;
      width: 1.3rem;
      height: 1.3rem;
      line-height: 1.4rem;
      background: $clr-2;
      text-align: center;
      font-weight: 700;
      font-size: $fs-16;
      color: white;
      counter-increment: numbered-discs;
      content: counter(numbered-discs);

      body.consulting & {background: $clr-b2;}
      body.academy & {background: $clr-b3;}
      body.resources & {background: $clr-b4;}
    }
  }
}

// numbered blocks

ol.numbered-blocks {
  counter-reset: numbered-blocks;

  &.alt.alt li:before {
    @extend .icon-extension,.icon-check;
    background: $clr-b1;
  }

  li {
    position: relative;
    margin-bottom: 4px;
    padding: $spacing;
    padding-left: $spacing*2;
    background: $clr-4;

    &:first-child {border-radius: $border-radius $border-radius 0 0;}
    &:last-child {border-radius: 0 0 $border-radius $border-radius;}

    &:before {
      position: absolute;
      right: 100%; top: 50%;
      margin: -1.25rem -1.25rem 0 0;
      border-radius: 50%;
      border: 4px solid white;
      padding: 0;
      width: 2.5rem;
      height: 2.5rem;
      line-height: 2.2rem!important;
      background: $clr-2;
      text-align: center;
      font-weight: 400;
      font-size: $fs-20;
      color: white;
      counter-increment: numbered-blocks;
      content: counter(numbered-blocks);
      box-sizing: border-box;

      body.consulting & {background: $clr-b2;}
      body.academy & {background: $clr-b3;}
      body.resources & {background: $clr-b4;}
    }

    > :last-child {margin-bottom: 0;}
  }
}

// contact details

ul.contact {
  li {
    position: relative;
    padding-left: 1.5rem;

    &:before {
      position: absolute;
      top: 0.2rem; left: 0;
      font-size: $fs-20;

      footer.footer & {
        top: 0;
      }
    }

    &.email {
      overflow-wrap: break-word;
      word-wrap: break-word;

      span {
        @include bp-s-less {@include hide-screen();}
        footer & {@include hide-screen();}
      }
    }

    &.address {
      div.box &,
      div.personalised-support &,
      footer & {@include hide-screen();}
      span {display: block;}
    }

    a {
      text-decoration: none;
      
      main & {
        @include bp-m {
          &:hover,
          &:focus {text-decoration: underline;}
        }
      }
    }

    span.icon-angle-right:before {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0.5rem;
      font-size: $fs-10;
    }
  }
}

// site map

ul.site-map {
  overflow: hidden;
  border-radius: $border-radius;

  > li {

    a {
      display: block;
      border-bottom: 1px solid white;
      padding: 0.5rem 1rem;
      background: $clr-3;
      text-decoration: none;
      color: $clr-1;

      @include bp-m {
        &:hover,
        &:focus {
          color: $clr-b1;
          box-shadow: inset 0 0 0 100rem rgba($clr-2,0.05);
        }
      }
    }

    ul {
      margin: 0;
      font-size: $fs-18;
      
      li a {
        padding-left: 2rem;
        background: $clr-4;
      }
    }

    &:last-child a {border-bottom: 0;}
  }
}

// faqs 

ol.faqs {
  counter-reset: faq-item;

  li {
    margin: 0 0 4px;
    counter-increment: faq-item;

    &:first-of-type h3 label  {border-radius: $border-radius $border-radius 0 0;}
    &:last-of-type h3 label,
    &:last-of-type div.answer  {border-radius: 0 0 $border-radius $border-radius;}
    &:last-of-type input:checked + h3 label {border-radius: 0;}

    input {
      @include hide-screen();

      + h3 {
        @extend .h4;
        margin: 0;
        color: $clr-b0; // important for when in a dark background strip
          
        label {
          position: relative;
          display: block;
          margin: 0;
          padding: $spacing;
          padding-right: 3rem;
          background: rgba($clr-4,0.7);
          font-weight: 400;
          color: $clr-1;
          cursor: pointer;
          user-select: none;
          -webkit-user-select: none;

          &:before {
            @extend .icon-extension,.icon-plus-circle;
            position: absolute;
            top: 50%; right: 0;
            z-index: 1;
            margin: -0.5rem 1rem 0 0;
            font-size: 1em;
            color: $clr-b1;
          }

          @include bp-m {
            &:hover,
            &:focus {
              background: $clr-3;
              color: $clr-b0;
            }
          }

          span:before {
            content: counter(faq-item) ". ";
          }
        }

        + div.answer {
          position: absolute;
          left: -1000em;
          padding: $spacing;
          background: white;
          color: $clr-1;
          -webkit-text-size-adjust: 100%; // forcing iOS to stop increasing text size

          :last-child {margin-bottom: 0;}
        }
      }

      &:checked {
        + h3 {

          label {
            background: $clr-2;
            color: white;

            &:before {
              @extend .icon-minus-circle;
              color: white;
            }
          }

          + div.answer {position: static;}
        }
      }
    }
  }
}

// services funnels

div.services-funnels {
  position: relative;

  @include bp-m {
    &:after {
      position: absolute;
      top: 0; bottom: 0; right: 0;
      z-index: 1;
      width: $col-6;
      background: rgba(black,0.3);
      content: "";
    }
  }

  ul {
    position: relative;
    z-index: 2;

    li {

      input {
        position: fixed;
        top: 50%; left: -1000em;
        z-index: -1;
        visibility: hidden;

        &:checked + div.content {
          @include bp-m {
            h3 {
              padding-right: 0; // hack to prevent word wrap jump

              label {
                padding-right: 4px; // hack to prevent word wrap jump
                background: rgba(black,0.3);
              }
            }

            div.text {
              right: 0;
              opacity: 1;
              transition: opacity 0.2s 0.2s;
            }
          }
        }
      }

      div.content {
        text-align: center;
        @include bp-m {text-align: left;}

        h3 {
          @extend .h2;
          margin: 0;

          @include bp-m {
            padding-right: 4px;
            width: $col-6;
            font-size: $fs-30;
          }

          label {
            display: block;
            background: rgba(black,0.1);
            font-weight: 300;

            // this anchor and span is a bit of a hack due to page jumping on mobile. Therefore on mobile the funnels remain closed and act as links to the service pages

            @include bp-m {
              position: relative;
              cursor: pointer;
              transition: background 0.2s;

              &:hover {
                background: rgba(black,0.2);
              }

              span {
                position: absolute;
                top: 0; left: 0; bottom: 0; right: 0;
              }
            }

            a {
              display: block;
              padding: $spacing;
              color: white;
              text-decoration: none;
            }
          }
        }

        div.text {
          display: none;

          @include bp-m {
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: absolute;
            top: 0; bottom: 0; right: 1000em;
            padding: $spacing ($spacing*1.5);
            width: $col-6;
            opacity: 0;
            transition: opacity 0.2s, right 0.2s 0.2s;
          }

          @include bp-l {padding: $spacing ($spacing*2);}

          p {
            @include bp-m {font-size: $fs-22;}
            @include bp-l {font-size: $fs-25;}

            &:first-child {font-weight: 300;}

            &:last-child {
              margin-bottom: 0;
              text-align: center;
            }

            a.button > span > span > span {@include hide-screen();}
          }
        }
      }
    }
  }
}

// direct funnels

ul.direct-funnels {
  position: relative;
  text-align: center;

  li.col.col {
    flex: 1 1 auto;

    a {
      @extend .h2;
      display: block;
      padding: ($spacing*1.5) $spacing;
      background: rgba($clr-1,0.25);
      color: white;
      text-decoration: none;
      cursor: pointer;
      transition: background-color 0.2s;

      @include bp-m {
        display: flex;
        flex: 1; 
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: ($spacing*2.5) $spacing;
        min-height: 10rem;

        &:hover,
        &:focus {
          background: rgba($clr-1,0.35);
          text-decoration: underline;
        }
      }

      span {
        display: block;
        margin-top: -1rem;

        span {
          display: inline-block;
          margin-bottom: 0.5rem;
          border-radius: 2rem;
          padding: 0.25rem 1rem;
          background: rgba(black,0.2);
          font-size: $fs-16;
          font-weight: 500;
          color: white;
        }
      }
    }
  }
}

// description funnels

ul.description-funnels {
  position: relative;
  text-align: left;

  li.col.col {
    flex: 1 1 auto;

    a {
      display: block;
      padding: $spacing;
      background: $clr-4;
      color: $clr-1;
      text-decoration: none;
      cursor: pointer;
      transition: background-color 0.2s, color 0.2s;

      @include bp-m {
        display: flex;
        flex-direction: column;
        flex: 1;
        padding: $spacing*1.5;

        &:hover h2,
        &:focus h2 {
          //color: $clr-b1;
          //text-decoration: underline;
        }
      }

      h2, h3 {
        margin-bottom: $vmargin/2;
        color: $clr-b1;

        span {
          display: block;
          margin-top: -0.5rem;
          @include bp-m {margin-top: -1rem;}

          span {
            display: inline-block;
            margin-bottom: 0.5rem;
            border-radius: 2rem;
            padding: 0.25rem 1rem;
            background: rgba(white,1);
            font-size: $fs-16;
            font-weight: 500;
            color: $clr-2;
          }
        }
      }

      @include bp-m {
        &:hover,
        &:focus {background: $clr-3;}
      }

      p {
        margin: 0 auto;
        max-width: 650px;
      }
    } 
  }
}

// team

ul.team {
  position: relative;
  text-align: center;

  > li {
    position: relative;
    @include bp-m {position: static;}

    div.content {
      display: flex;
      flex: 1;

      h4 {
        @extend .h2;
        position: relative;
        display: flex;
        flex: 1;
        margin: 0;

        label {
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          flex: 1;
          padding: ($spacing*1.5) $spacing;
          background: $clr-4;
          cursor: pointer;
          transition: background-color 0.2s, color 0.2s;

          @include bp-m {
            padding: ($spacing*2.25) $spacing;

            &:hover,
            &:focus {background: $clr-3;}
          }

          @include bp-l {
            padding: ($spacing*1.75) $spacing;
          }

          span.image {
            display: block;
            position: relative;
            overflow: hidden;
            margin: 0 auto $vmargin;
            border-radius: 50%;
            width: 200px;

            img {
              position: absolute;
              top: 0; left: 0;
              border-radius: 50%;
              width: 100%;
            }

            &:before {
              display: block;
              padding-top: 100%;
              content: "";
            }
          }

          span {
            &.name {
              font-weight: 300;  
              color: $clr-b1;
            }

            &.title {
              display: block;
              margin-top: 0.25rem;
              font-weight: 400;
              font-size: $fs-18;
            }
          }
        }

        a.linkedin.linkedin {
          display: none;
          margin-top: 0.75rem;
          padding-left: 0.6rem;
          padding-right: 0.6rem;

          @include bp-m {
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 0; left: 50%;
            margin: 75px 0 0 125px;
          }

          &:before {
            margin: 0;
            font-size: $fs-20;
            color: white;
          }

          span {@include hide-screen();}
        }
      }

      div.text {
        position: absolute;
        left: -1000em;
        align-items: flex-start;
        text-align: left;
        color: white;

        a.button span {@include hide-screen();}

        > div:last-child p {margin-bottom: 0;}
      }
    }

    input {
      @include hide-screen();

      &:checked + div.content {
        display: block; // reset flex
        padding: ($spacing*1.5) $spacing;
        background: $clr-2;

        @include bp-m {
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          z-index: 1; // required
          padding: $spacing*2;
        }

        &:after {
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          z-index: 1;
          @extend .aesthetic;
          background-repeat: no-repeat;
          background-position: 50%;
          background-size: cover;
          opacity: 0.1;
          content: "";
        }

        h4,
        div.text {
          position: relative;
          z-index: 2;
          left: auto;
          > :last-child {margin-bottom: 0;}
        }

        h4 {
          display: block; // reset flex
          margin-bottom: ($vmargin/2);
          color: white;

          @include bp-m {margin-bottom: $vmargin;}

          label {
            display: block;
            padding: 0;
            min-height: 0;
            background: none;
            cursor: default;
            transition: none;

            @include bp-m {
              display: inline-block;

              &:hover,
              &:focus {
                span.name {text-decoration: none;}
              }
            }

            &:before {
              @extend .icon-extension,.icon-times;
              display: flex;
              align-items: center;
              justify-content: center;
              position: absolute;
              right: -0.5rem; top: -1rem;
              border-radius: 50%;
              width: 1.5rem;
              height: 1.5rem;
              background: white;
              text-align: center;
              font-size: $fs-20;
              color: $clr-2;
              cursor: pointer; 

              @include bp-m {
                right: -1rem; top: -1rem;
                width: 2rem;
                height: 2rem;
                font-size: $fs-24;
              }
            }

            &:hover:before {
              background: $clr-b1;
              color: white;
              transition: color 0.2s, background-color 0.2s;
            }

            span.name {color: white;}
          }

          a.linkedin {
            display: inline-block;
            @include bp-m {display: flex;}
          }
        }
      }
    }
  }
}

// testimonials

div.testimonials {
  margin: $vmargin 0;
  background: url(../../assets/images/content/other/world-map-mob.png) no-repeat 50%;
  background-size: auto 100%;

  @include bp-m {
    margin: ($vmargin*2) 0;
    background-image: url(../../assets/images/content/other/world-map.png);
  }

  ul {
    margin-bottom: 0;
    text-align: center;

    li {

      input {
        position: fixed;
        top: 50%; left: -1000em;
        z-index: -1;
        visibility: hidden;

        &:checked {
          + div.content {
            overflow: visible;
            visibility: visible;
            margin-left: 0;
            padding: 1.25rem 0;
            width: 100%;
            max-height: 30rem;
            opacity: 1;
            transition: opacity 0.25s 0.25s, margin-left 0.25s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

            @include bp-m {padding: 3rem 0;}
          }
        }
      }

      div.content {
        display: flex;
        justify-content: center;
        overflow: hidden;
        visibility: hidden;
        margin-left: -20rem;
        padding: 0;
        width: 0;
        max-height: 0;
        opacity: 0;
        transition: opacity 0.25s, margin-left 0.25s, visibility 0s 0.25s, width 0s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

        blockquote {
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-bottom: 0;
          border-left: 5px solid $clr-b1;
          border-radius: $border-radius;
          padding: $spacing ($spacing/1.7);
          max-width: 650px;
          min-height: 18rem;
          background: rgba($clr-4,0.5);

          @include bp-s {
            padding: $spacing;
            min-height: 14rem;
          }

          @include bp-m {padding: $spacing ($spacing*1.5);}

          p {
            @extend .h4;

            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          }

          cite {
            font-style: normal;
            @include bp-m-less {font-size: $fs-18;}
            span {display: block;}
          }
        }

        label.button {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          right: 50%; top: 100%;
          z-index: 10;
          margin: -1.25rem -1.25rem 0 0;
          border-radius: 3rem;
          padding: 0!important;
          width: 2.5rem;
          height: 2.5rem;
          font-size: $fs-25;

          @include bp-m {
            right: 0; top: 50%;
            margin: -1.25rem -1.25rem 0 0;
          }

          &:before {
            @extend .icon-extension, .icon-angle-right;
          }

          span {@include hide-screen();}
        }
      }
    }
  }
}

// downloads

ul.downloads {
  margin-bottom: 0;

  > li {
    margin-top: 4px;
    &:first-child {margin-top: 0;}

    @include bp-m {
      &.special {
        text-align: center;
        h3 span.info {justify-content: center;}
      }
    }

    div.content {
      flex: 1;
      display: block;
      padding: $spacing;
      background: $clr-4;

      @include bp-m {padding: $spacing*1.5;}

      > :last-child {margin-bottom: 0;}

      h3 {
        margin-bottom: $vmargin/2;

        span.info {
          display: flex;
          margin-bottom: 0.5rem;
          font-weight: 500;
          font-size: $fs-16;

          > span {
            flex: 0 1 auto;
            padding: 0.15rem 0.5rem;

            &:first-child {
              border-top-left-radius: 0.2rem;
              border-bottom-left-radius: 0.2rem;
            }

            &:last-child {
              border-top-right-radius: 0.2rem;
              border-bottom-right-radius: 0.2rem;
            }

            &.value {
              background: $clr-2;
              color: white;
              
              &.premium {background: $clr-b4;}
            }

            &.asset {background: $clr-3;}
            &.file {background: white;}
          }
        }

        span.heading {display: block;}
      }

      a.button {
        @include bp-s-less {display: block;}
      }
    }
  }
}

// events
// * provides white grid border effect whilst giving the list a background colour 

ol.events {
  text-align: left;
  @include bp-m {background: rgba($clr-4,0.5);}// *

  li.col {
    @include bp-m {
      box-shadow: 0 0 0 2px white, 0 4px 0 0 white;// *
      &:last-child {box-shadow: 0 0 0 2px white, inset -2px 0 0 0 white;}// *
    }

    div.content {
      flex: 1;
      display: block;
      padding: $spacing;
      background: $clr-4;

      div.section.style-5 & {background: rgba(white,0.5);}
      
      @include bp-m {padding: $spacing*1.5;}

      > :last-child {margin-bottom: 0;}

      h3 {

        span {
          display: block;

          b {
            display: inline-block;
            margin-bottom: 0.5rem;
            border-radius: 0.2rem;
            padding: 0.15rem 0.5rem;
            background: $clr-b3;
            font-weight: 500;
            font-size: $fs-16;
            color: white;
          }
        }

        a {
          display: inline-block;
          text-decoration: none;

          @include bp-m {
            &:hover,
            &:focus {text-decoration: underline;}
          }
        }
      }

      p.cta {
        a.button {
          @include bp-s-less {display: block;}
          span {@include hide-screen();}
        }
      }
    }
  }
}

// events details

ul.events-details {
  overflow: hidden;
  border-radius: $border-radius;
  padding: 1rem;
  background: $clr-4;
  font-size: $fs-18;

  ol.events & {background: white;}

  @include bp-s {padding: 0;}
 
  li {
    @include bp-s {display: flex;}

    b {
      font-weight: 500;

      @include bp-s {
        display: inline-block;
        flex: 0 0 auto;
        padding: 0.5rem;
        min-width: 9rem;
        background: $clr-3;
        text-align: right;
      }
    }

    span {

      @include bp-s {
        display: inline-block;
        flex: 1;
        padding: 0.5rem;
        box-shadow: inset 0 -1px rgba(black,0.05);
      }

      small {font-size: $fs-16;}
    }

    &:last-child > * {box-shadow: none;}
  }
}

// articles
// * provides white grid border effect whilst giving the list a background colour 

ol.articles {
  @include bp-m {background: rgba($clr-4,0.5);}// *

  li.col {

    @include bp-m {
      box-shadow: 0 0 0 2px white, 0 4px 0 0 white;// *
      &:last-child {box-shadow: 0 0 0 2px white, inset -2px 0 0 0 white;}// *
    }

    a {
      display: block;
      background: $clr-4;
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.2s, color 0.2s;

      @include bp-m {
        &:hover,
        &:focus {
          background: $clr-3;

          h3 {
            //color: $clr-b1;
            //text-decoration: underline;
          }
        }
      }

      figure {
        position: relative;
        z-index: 1;
        margin-bottom: 0;

        &:before { // stops page jump on slow loading of images
          position: relative;
          z-index: 1;
          display: block;
          padding-top: 56.3%;
          background: $clr-3;
          content: "";
        }

        img {
          position: absolute;
          top: 0; left: 0;
          z-index: 2;
        }
      }

      div.text {
        padding: $spacing;
        
        h3 {
          @extend .h4;
          margin-bottom: $vmargin/2;
          font-weight: 400;
          color: $clr-b0;
        }

        p {
          margin-bottom: 0;
          color: $clr-1;

          time {font-size: $fs-14;}
        }
      }
    }
  }
}

// information

ul.information {

   div.content {
    flex: 1;
    background: rgba(white,0.5);

    h3 {
      @extend .h2;
      margin: 0;
      padding: ($spacing*0.75) $spacing;
      background: rgba(white,0.6);
      text-align: center;
    }

    p {
      margin: 0;
      padding: $spacing;
      font-size: $fs-22;

      @include bp-m {padding: $spacing*1.5;}

      em {
        display: block;
        margin-bottom: $vmargin/2;
        font: italic $fs-30 Georgia, Serif;
      }
    }
  }

  &.alt div.content {
    background: $clr-4;

    h3 {background: $clr-3;}
  }

  &.alt-2 div.content {
    background: rgba(black,0.2);

    h3 {background: rgba(black,0.5);}
  }
}

// guide navigation

// hiding the guide contents button doesn't work in webkit on scroll - there is a translatez(n) fix, but maybe try later
// body.guide {
//   main div.strip,
//   footer.footer {
//     position: relative;
//     z-index: 3;

//     &.guide-intro,
//     &.guide-content {z-index: 1;}
//   }

//   nav.guide-nav {z-index: 2;}
// }

input.guide-nav-toggle {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;
  visibility: hidden;

  &:checked + nav.guide-nav {
    z-index: 2000;

    @include bp-m {left: 0;}

    label.cta {
      span.button {
        box-shadow: inset 0 1px 2px rgba(0,0,0,0.75), 0 0 0 5px rgba(white,0.1);
        &:before {@extend .icon-times;}
      }

      &:after {@include bp-m {content: "";}}
    }

    div.guide-links {
      right: 0; left: 0;

      @include bp-m {
        right: auto; left: auto;
        box-shadow: 0.25rem 0 1.5rem rgba(black,0.3);
      }
    }

    + article div.guide-intro p.cta label span[class*="icon-"]:before,
    + article div.guide-content div.guide-nav-end label span[class*="icon-"]:before {@extend .icon-times;}
  }
}

nav.guide-nav {
  position: fixed;
  right: 0; bottom: 0;
  padding-bottom: 1rem;
  z-index: 2000;

  @include bp-m {
    right: auto; top: 0; left: -27rem;
    padding-bottom: 0; 
    width: 27rem;
    display: flex;
    align-items: stretch;
    flex-direction: column;
    transition: left 0.25s;
  }

  label.cta {
    cursor: pointer;
      
    span.button {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
      margin-right: 1rem;
      border-radius: 3rem;
      padding: 0!important;
      width: 3rem;
      height: 3rem;

      @include bp-m {
        position: absolute;
        bottom: 0; left: 100%;
        margin: 0 0 1rem 1rem;
      }

      &:before {
        margin: 0!important;
        font-size: $fs-30;
      }

      span {@include hide-screen();}
    }

    &:after {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      background: rgba(black,0.3);
    }
  }

  div.guide-links {
    position: fixed;
    left: -1000em; top: 0; bottom: 0;
    z-index: 1;
    background: $clr-1;
    text-align: center;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;

    @include bp-m {
      flex: 1;
      position: relative;
      left: auto; top: auto; bottom: auto;
      box-shadow: 0 0 0 rgba(black,0.3);
      transition: box-shadow 0.25s;
    }

    h2 {
      @extend .h4;
      margin: 0;
      padding: 0.75rem 1rem;
      background: rgba(black,0.3);
      color: white;
      font-weight: 300;

      @include bp-m {text-align: left;}
    }

    > ul {
      display: inline-block;
      margin-bottom: 0;
      padding: 0.75rem 0.5rem 2rem 1px;
      font-size: $fs-18;
      text-align: left;

      @include bp-m {
        display: block;
        font-size: $fs-20;
      }

      ul {
        margin-bottom: 0;

        li {
          margin-left: 1.3rem;
          border-left: 1px solid rgba(white,0.2);
        }
      }

      > li {

        > ul {
          padding-left: 0.5rem;
          > li {margin-left: 0.55rem;}
        }

        > a { 
          padding-left: 1rem;
          font-weight: 500;

          &:before {content: none;}
        }
      }

      a {
        display: flex;
        border-radius: 0 0.25rem 0.25rem 0;
        padding: 0.2rem 0.5rem;
        padding-left: 0;
        text-decoration: none;
        color: white;
        
        @include bp-m {
          &:hover,
          &:focus {background: rgba(white,0.075);}
        }

        &.current {
          background: rgba(black,0.3);
          box-shadow: -1px 0 0 $clr-b1, inset 4px 0 0 $clr-b1;
          &:before {color: $clr-b1;}
        }

        &:before {
          flex: 0 1 auto;
          color: rgba(white,0.2);
          content: "\02014\000A0";
        }

        span {flex: 1 1 auto;}
      }
    }
  }
}

// share options

ul.share-options {
  margin-bottom: 0;
  font-size: 1rem;

  li {
    display: inline-block;
    margin-right: 0.25rem;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 3rem;
      width: 1.7rem;
      height: 1.7rem;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      &.icon-linkedin {background: #0077B5;}
      &.icon-twitter {background: #55ADEE;}
      &.icon-facebook {background: #4267B2;}
      &.icon-envelope {background: #7D7D7D;}

      @include bp-m {
        &:hover,
        &:focus {box-shadow: inset 0 0 0 4rem rgba(black,0.1);}
      }

      span {@include hide-screen();}
    }

    &:last-child {margin-right: 0;}
  }
}

// related resources - service pages

div.related-resources {
  border-radius: $border-radius;
  padding: $spacing;
  background: rgba(white,0.4);

  h3 {
    //@extend .h4;
    margin-bottom: 0.5rem;
  }

  ul {
    margin-bottom: 0;
    //font-size: $fs-18;

    li {
      margin: 0.2rem 0;

      a {
        position: relative;
        display: inline-block;
        padding-left: 1.4em;
        color: $clr-1;
        text-decoration: none;

        @include bp-m {
          &:hover,
          &:focus {color: $clr-b0;}
        }

        &:before {
